a {
  color: inherit;
  text-decoration: none;
}

.navbar {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.navbar span {
  font-size: 1rem;
  text-shadow: 2px 2px #44444c;
}

@media (min-width: 600px) {
  .navbar {
    padding-right: 5rem;
  }

  .navbar span {
    font-size: 1.2rem;
  }
}
